      this.scene.pan2Dxyzmm[3] = zoomResult.newZoom;
      this.scene.pan2Dxyzmm[0] += zoomResult.panOffsetX;
      this.scene.pan2Dxyzmm[1] += zoomResult.panOffsetY;
      this.scene.pan2Dxyzmm[2] += zoomResult.panOffsetZ;
