    const extentsMax = this.volumeObject3D.extentsMax;
    let mnMM = vec323.fromValues(extentsMin[0], extentsMin[1], extentsMin[2]);
    let mxMM = vec323.fromValues(extentsMax[0], extentsMax[1], extentsMax[2]);
    mnMM = this.swizzleVec3MM(mnMM, axCorSag);
    mxMM = this.swizzleVec3MM(mxMM, axCorSag);
