      mnMM2 = this.swizzleVec3MM(mnMM2, axCorSag);
      mxMM2 = this.swizzleVec3MM(mxMM2, axCorSag);
      const fovMM2 = vec323.create();
      vec323.subtract(fovMM2, mxMM2, mnMM2);
