    const eMin = this.volumeObject3D.extentsMin;
    const eMax = this.volumeObject3D.extentsMax;
    let mnMM = vec323.fromValues(eMin[0], eMin[1], eMin[2]);
    let mxMM = vec323.fromValues(eMax[0], eMax[1], eMax[2]);
    const rotation = mat417.create();
    mnMM = this.swizzleVec3MM(mnMM, axCorSag);
    mxMM = this.swizzleVec3MM(mxMM, axCorSag);
    const fovMM = vec323.create();
    vec323.subtract(fovMM, mxMM, mnMM);
