      screen.fovMM[1] *= zoomH;
      let center = (screen.mnMM[0] + screen.mxMM[0]) * 0.5;
      screen.mnMM[0] = center - screen.fovMM[0] * 0.5;
      screen.mxMM[0] = center + screen.fovMM[0] * 0.5;
      center = (screen.mnMM[1] + screen.mxMM[1]) * 0.5;
