        mm[dimH] = linesV[i];
        let frac = this.mm2frac(mm);
        frac = frac[dimH];
        let left = this.frac2mm([fracX, frac, 0]);
        left = swizzleVec3(left, [1, 2, 0]);
        let right = this.frac2mm([fracX, frac, 1]);
